import { AfterViewInit, Component, Input, OnChanges, OnInit, SimpleChanges, ViewChild } from '@angular/core';
import { S1Application, UserAccount } from '@models';
import { ApiService } from '@services';
import { APP_DECISION_STATUS, ATTACHMENT_TYPE, ENDPOINTS, SUBMISSION_TYPE } from '@shared';
import { MatTableDataSource } from '@angular/material/table';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { ApplicationSingleMedia } from '../../../../../core/models/application-single-media.model';

@Component({
  selector: 'app-add-payment-preview-application',
  templateUrl: './add-payment-preview-application.component.html',
})

export class AddPaymentPreviewApplicationComponent implements OnChanges {
  @Input() previewS1Application = new S1Application();
  @Input() applicationSingleMedia = new ApplicationSingleMedia();
  @Input() multimediaDTO: any = {};
  presentorDetails = new UserAccount();

  multimediaMovingList = [];
  multimediaStaticList = [];

  constructor(private apiService: ApiService) { }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['previewS1Application']) {
      this.getPresentorDetails();
    }
  }

  getPresentorDetails() {
    if (this.previewS1Application?.applicationOwner?.username) {
      this.apiService.findByParam(ENDPOINTS.userLogin, this.previewS1Application.applicationOwner.username).subscribe(
        (res: any) => {
          if (res.responseData.data) {
            this.presentorDetails = res.responseData.data;
          }
        });
    }
  }
}
